@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap);
:root,
:root.theme-1 {
  --main-text: hsl(0, 0%, 100%);
  --btn-del-res-text: hsl(0, 0%, 100%);
  --btn-main-text: hsl(221, 14%, 31%);
  --equals-btn-text: hsl(0, 0%, 100%);
  --main-background: hsl(222, 26%, 31%);
  --screen-background: hsl(224, 36%, 15%);
  --toggle-background: hsl(223, 31%, 20%);
  --btn-main-background: hsl(30, 25%, 89%);
  --btn-main-shadow: hsl(28, 16%, 65%);
  --btn-del-res-background: hsl(225, 21%, 49%);
  --btn-del-res-shadow: hsl(224, 28%, 35%);
  --btn-equals-background: hsl(6, 63%, 50%);
  --btn-equals-shadow: hsl(6, 70%, 34%);
}

:root.theme-2 {
  --main-text: hsl(60, 10%, 19%);
  --btn-main-text: hsl(60, 10%, 19%);
  --equals-btn-text: hsl(0, 0%, 100%);
  --main-background: hsl(0, 0%, 90%);
  --screen-background: hsl(0, 0%, 93%);
  --toggle-background: hsl(0, 5%, 81%);
  --btn-main-background: hsl(45, 7%, 89%);
  --btn-main-shadow: hsl(35, 11%, 61%);
  --btn-del-res-background: hsl(185, 42%, 37%);
  --btn-del-res-shadow: hsl(185, 58%, 25%);
  --btn-equals-background: hsl(25, 98%, 40%);
  --btn-equals-shadow: hsl(25, 99%, 27%);
}

:root.theme-3 {
  --main-text: hsl(52, 100%, 62%);
  --btn-del-res-text: hsl(52, 100%, 62%);
  --btn-main-text: hsl(52, 100%, 62%);
  --equals-btn-text: hsl(198, 20%, 13%);
  --main-background: hsl(268, 75%, 9%);
  --screen-background: hsl(268, 71%, 12%);
  --toggle-background: hsl(268, 71%, 12%);
  --btn-main-background: hsl(268, 47%, 21%);
  --btn-main-shadow: hsl(290, 70%, 36%);
  --btn-del-res-background: hsl(281, 89%, 26%);
  --btn-del-res-shadow: hsl(285, 91%, 52%);
  --btn-equals-background: hsl(176, 100%, 44%);
  --btn-equals-shadow: hsl(177, 92%, 70%);
}

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 3.2rem;
  line-height: 1.5;
  font-family: 'Spartan', sans-serif;

  background-color: var(--main-background);
}

#root {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.calc {
  width: 54rem;
  max-width: calc(100% - 2rem);
  height: 70rem;
  background-color: transparent;
  margin: 2rem;
}

@media (max-width: 580px) {
  .calc {
    width: 95vw;
  }
}

.ThemeSwitcher_switcher__c1sng {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
}

.ThemeSwitcher_switcher__c1sng p {
  font-size: 1.2rem;
  margin-right: 3rem;
}

.ThemeSwitcher_themes__z4KBv {
  background-color: var(--toggle-background);
  width: 72px;
  height: 26px;
  border-radius: 15px;
  position: relative;
}

.ThemeSwitcher_themes__z4KBv div {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: absolute;
  top: -150%;
}

.ThemeSwitcher_theme_btn__2lp4n {
  border: none;
  background-color: transparent;
  color: inherit;
  cursor: pointer;
  font-weight: bold;
  height: 6rem;
  width: 2rem;
}

.ThemeSwitcher_slider__1eJYc {
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 50%;
  background-color: var(--btn-equals-background);
  position: absolute;
  top: 0.4rem;
  left: 0.5rem;
  transition: all 0.3s;
}

.ThemeSwitcher_slider__1eJYc.ThemeSwitcher_theme-1__2Jaz2 {
  left: 0;
  transform: translateX(0.5rem);
}
.ThemeSwitcher_slider__1eJYc.ThemeSwitcher_theme-2__17r8m {
  left: 25%;
  transform: translateX(50%);
}
.ThemeSwitcher_slider__1eJYc.ThemeSwitcher_theme-3__1GA04 {
  left: 50%;
  transform: translateX(calc(100% - 0.5rem));
}

.Header_header__1GPgl {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--main-text);
  font-size: 3rem;
}

.Display_display__2tPjP {
  width: 100%;
  height: 12.5rem;
  background-color: var(--screen-background);
  display: grid;
  text-align: right;
  border-radius: 1rem;
  padding: 2.5rem;
  color: var(--main-text);
  margin-top: 3.2rem;
}

.Display_previous_value__dq92H {
  font-size: 1.6rem;
}

.Display_current_value__2X76s {
  font-size: 5rem;
}

@media (max-width: 500px) {
  .Display_display__2tPjP {
    height: 9rem;
  }

  .Display_previous_value__dq92H {
    font-size: 1.2rem;
  }

  .Display_current_value__2X76s {
    font-size: 3.2rem;
  }
}

.ButtonPad_pad__nGiNG {
  width: 100%;
  height: 47rem;
  background-color: var(--toggle-background);
  border-radius: 1rem;
  margin-top: 2.5rem;
  padding: 3.2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;

  grid-template-areas: 'seven eight nine delete' 'four five six add' 'one two three subtract' 'decimal zero divide multiply' 'reset reset equal equal';
}

@media (max-width: 580px) {
  .ButtonPad_pad__nGiNG {
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
}

@media (max-width: 500px) {
  .ButtonPad_pad__nGiNG {
    height: 42rem;
    padding: 2.4rem;
  }
}

